import service from "@/utils/request";

// 2022/3/30 9:21
export function Login(data){
    return service({
        url:'/api/user/login',
        method:'post',
        data:data
    })
}

//2022/3/30 10:21
export function Register(data){
    return service({
        url:'/api/user/register',
        method:'post',
        data:data
    })
}

//2022/3/30 11:13
export function UserInfo(data){
    return service({
        url:'/api/user/id',
        method:'post',
        data:data,
    })
}

//2022/3/31 8:37
export function SearchFilm(data){
    return service({
        url:'/api/film/name',
        method:'get',
        data:data
    })
}
