import service from "@/utils/request";

//2022/5/19 11:13
export function QueryCart(uid){
    return service({
        url:'/api/cart/'+uid,
        method:'get',
    })
}

//2022/3/30 11:13
export function DeleteCart(vo){
    return service({
        url:'/api/cart/',
        method:'delete',
        data:vo,
    })
}